.canvasContainer {
  left: 0;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.tag {
  position: absolute;
  color: white;
  padding: 12px 16px;
  background-color: rgba(19, 19, 20, 1);
  font-size: 18px;
  border-radius: 4px;
  border-image: linear-gradient(#4c4c55, #27272a) 1;
  border-width: 0.4px;
  border-style: solid;
  z-index: 1; /* 태그의 z-index를 높여서 도형 위에 나타나도록 설정 */
}

.canvas {
}

.tags{
  position: absolute;
  left: 50%;
  transform: translateX(10%);
  height: 100vh;
  width: 1000px;
}

.tag:nth-child(1) {
  top: 50%;
  left: 40%;
}
.tag:nth-child(2) {
  top: 20%;
}

.tag:nth-child(3) {
  top: 80%;
  left: 10%;
}

@media screen and (max-width: 900px) {
  .canvas{
    display: none;
  }

  .tag{
    display: none;
  }
}