@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;600;700&display=swap");

.FacultyCard {
  color: var(--G1);
  background-color: var(--G9);
  display: flex;
  padding: 35px 30px;
  gap: 30px;
  border-radius: 12px;
  font-weight: 300;
  flex:1;
}

.Photo img {
  border-radius: 12px;
  width: 175px;
  height: 233px;
}

.Content {
  display: flex;
  gap: 16px;
  flex-direction: column;
  justify-content:space-between;
  width: 100%;
}

.Content h1 {
  font-size: 28px;
  font-weight: 400;
}

.Info {
  display: flex;
  gap: 30px;
  flex-direction: column;
}

.professor {
  display: flex;
  flex-direction: column;
}

.professor > div {
  font-size: 14px;
  margin-bottom: 10px;
  color: var(--G4);
  word-break: keep-all;
}

.professor b {
  font-weight: 400;
}

.professor .position{
  font-size: 20px;
}
.professor .position_detail{
  font-size: 14px;
}

.History {
  font-size: 13px;
  color: var(--G4);
}

.History ul {
  margin-left: 16px;
}

.History ul li {
  font-size: 14px;
  margin-bottom: 2px;
}
@media screen and (max-width: 1324px) {
  .FacultyCard {
    flex-direction: column;
    align-items: center;
  }

  .FacultyCard .Info {
    flex-direction: column;
  }
}

@media screen and (max-width: 1124px) {
  .Content .name {
    font-size: 20px;
  }
  .Content .Info * {
    font-size: 14px;
  }
}

@media screen and (max-width: 900px) {

}
